import React, { Component } from 'react';
import { List, Avatar,Icon } from 'antd';

import './blog.css';


const URL = 'https://api.rss2json.com/v1/api.json?rss_url=https://medium.com/feed/@LanceUlanoff';


export default class BlogPage extends Component {


    constructor(props){
        super(props);
        this.state = {
          posts:[]
        };
    }

    componentDidMount(){
      fetch(URL)
          .then((res) => res.json())
              .then((data) => {
                      const items = data.items;
                      const posts = items.filter(item => item.categories.length > 0);
                      this.setState({posts});
                     
                  });
    }

  render() {
    return (
        <div>
            <header>        
                <div className='container'>
                     <h1 className='intro-name'>Nivāta InfoSec</h1>
                     <h1 className="blog"><Icon type="read" />  Blog</h1>
                </div>
            </header>
            <div className="blogdiv">
                <List
                    itemLayout="horizontal"
                    dataSource={this.state.posts}
                    bordered = "true" 
                    style={{padding:'10px',marginBottom:'10px'}}
                    renderItem={item => (
                    <List.Item style={{padding:'10px',marginBottom:'10px'}}>
                        <List.Item.Meta
                        avatar={ <Avatar src={item.thumbnail} size = {100} shape ="square"/>}
                        title={<a href={item.link} target="_blank" rel="noopener noreferrer" className="title-a">{item.title}</a>}
                        description={<span style={{fontWeight:600}}>Published on {item.pubDate.split(" ")[0]} </span> }
                        />
                    
                    
                    </List.Item>
                    )}
                >
                </List>
             </div>
        </div>
     
    )
  }
}
